.main_div{
    background: #87cfe6;
    position: fixed;
    width: 100%;
    height: 100%;
}

.inner_div{
    width: 400px;
    background: #e4c0dd;
    margin: 0 auto;
    height: 400px;
}
.inner_center_div{
    display: flex;
    align-items: center;
    height: 100%;
}
.input_filed{
    display: flex;
    justify-content: center;
    padding: 10px 10px;
    height: 50px;
}

.input_filed button{
    padding: 20px;
}
.todo_list li{
    display: inline-block;
}
.todo_list{
    display: flex;
    gap: 10px;
    justify-content: center;
}
.inner_div h1{
    text-align: center;
    color: rebeccapurple;
    font-size: 25px;
}